*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  color: inherit;
  font-size: inherit;
  font-weight: inherit;
  font-family: inherit;
}

span.colored {
  color: #FCF306;
}

.App footer {
  display: block;
  position: relative;
  width: 100%;
  height: 250px;
  background-color: white;
  background-image: transparent;
  border-radius: 15px 15px 0 0;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
  border: 2px solid #FCF306;
  background-color: rgb(255, 254, 238);
}/*# sourceMappingURL=App.css.map */